export default {
  layout: 'empty',

  data: () => ({
    bs: true
  }),

  computed: {
    store () {
      return this.$store.state.store
    }
  },

  mounted() {
    const self = this

    setTimeout(() => {
      window.location.replace(`https://wa.me/${self.store.whatsapp}`)
    }, 3000)
  }
}
